html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  height: 100%;
  margin: 0 auto;
  padding: 0;
  font-family: $roboto;
  font-size: 15px;
  line-height: 22px;
  font-weight: 400;
  color: $black;
  background-color: $white;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

input:invalid,
textarea:invalid {
  box-shadow: none;
  outline: none;
}
