@mixin container {
  max-width: 1260px;
  margin: 0 auto;
  padding-left: 45px;
  padding-right: 45px;
}

@mixin padding-tablet {
  padding-left: 22px;
  padding-right: 22px;
}

@mixin padding-mobile {
  padding-left: 20px;
  padding-right: 20px;
}

@mixin reset-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin reset-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

@mixin text($font-family, $font-size, $line-height) {
  margin: 0;
  font-family: $font-family;
  font-size: $font-size;
  line-height: $line-height;
}

@mixin button($width, $min-height, $font-size, $line-height, $color) {
  display: inline-block;
  width: $width;
  min-height: $min-height;
  padding: 10px 15px;
  font-family: $roboto;
  font-size: $font-size;
  font-weight: 500;
  line-height: $line-height;
  color: $color;
  text-align: center;
}

@mixin arrow($width, $height, $deg) {
  content: "";
  position: absolute;
  width: $width;
  height: $height;
  border: 1px solid $white;
  border-top-style: none;
  border-right-style: none;
  transform: rotate($deg);
}
