@font-face {
  font-family: "Roboto";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Roboto-Regular"), url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local("Roboto-Medium"), url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff"); }

@font-face {
  font-family: "Roboto";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("Roboto-Bold"), url("../fonts/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto-Bold.woff") format("woff"); }

@font-face {
  font-family: "Roboto";
  font-weight: 900;
  font-style: normal;
  font-display: swap;
  src: local("Roboto-Black"), url("../fonts/Roboto-Black.woff2") format("woff2"), url("../fonts/Roboto-Black.woff") format("woff"); }

@font-face {
  font-family: "Raleway";
  font-weight: 900;
  font-style: normal;
  font-display: swap;
  src: local("Raleway-Black"), url("../fonts/Raleway-Black.woff2") format("woff2"), url("../fonts/Raleway-Black.woff") format("woff"); }

@font-face {
  font-family: "Circe";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Circe-Regular"), url("../fonts/Circe-Regular.woff2") format("woff2"), url("../fonts/Circe-Regular.woff") format("woff"); }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  height: 100%;
  margin: 0 auto;
  padding: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 15px;
  line-height: 22px;
  font-weight: 400;
  color: #000000;
  background-color: #ffffff; }

a {
  text-decoration: none; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

input:invalid,
textarea:invalid {
  box-shadow: none;
  outline: none; }

.header__wrapper {
  max-width: 1260px;
  margin: 0 auto;
  padding-left: 45px;
  padding-right: 45px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding-top: 20px;
  padding-bottom: 20px; }

.header__logo {
  margin-right: 5.1%; }

.header__toggle {
  display: none; }

.header__menu {
  width: 370px; }

.header__menu__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  align-items: center; }

.header__menu__item {
  font-size: 10px;
  font-weight: 500;
  line-height: 20px;
  text-transform: uppercase;
  letter-spacing: 0.06em;
  color: #707070; }
  .header__menu__item:not(:last-child) {
    margin-right: 20px; }
  .header__menu__item:first-child {
    margin-right: 24px; }

.header__menu__item a {
  padding: 10px 0;
  color: #707070; }
  .header__menu__item a:hover, .header__menu__item a:focus {
    color: #000000; }

.header__contacts {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  width: 370px;
  margin-left: auto; }

.header__phone {
  font-size: 18px;
  font-weight: 700;
  line-height: 20px;
  color: #1c1c1c; }

.header__button {
  display: inline-block;
  width: 191px;
  min-height: 45px;
  padding: 10px 15px;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 22px;
  color: #000000;
  text-align: center;
  background-color: #00ffd7; }
  .header__button:hover, .header__button:focus {
    box-shadow: 0 2px 4.75px 0.25px rgba(0, 255, 215, 0.6); }

@media (max-width: 1023px) {
  .header__wrapper {
    position: relative;
    padding: 8px 64px 7px; }
  .header__logo {
    width: 86px;
    height: 37px;
    margin-right: 37px; }
  .header__toggle {
    background-color: transparent;
    border: none;
    cursor: pointer;
    position: relative;
    display: block;
    padding: 0;
    width: 26px;
    height: 21px; }
    .header__toggle::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 26px;
      height: 2px;
      background-color: rgba(0, 0, 0, 0.3);
      box-shadow: 0 10px 0 0 rgba(0, 0, 0, 0.3), 0 19px 0 0 rgba(0, 0, 0, 0.3); }
    .header__toggle--open {
      width: 21px;
      height: 21px; }
      .header__toggle--open::before, .header__toggle--open::after {
        content: "";
        position: absolute;
        top: 10px;
        left: -4px;
        width: 28px;
        height: 2px;
        background-color: rgba(0, 0, 0, 0.3);
        box-shadow: none; }
      .header__toggle--open::before {
        transform: rotate(45deg); }
      .header__toggle--open::after {
        transform: rotate(-45deg); }
  .header__menu {
    display: none;
    position: absolute;
    top: 53px;
    left: 0;
    width: 207px;
    min-height: 160px;
    background-color: #e7e7e7; }
    .header__menu--open {
      display: block; }
  .header__menu__list {
    display: block; }
  .header__menu__item {
    font-family: "Circe", "Arial", sans-serif;
    font-size: 12px;
    font-weight: 400;
    line-height: 12px;
    color: #383838;
    text-transform: uppercase; }
    .header__menu__item:not(:last-child) {
      margin-right: 0;
      border-bottom: 1px solid rgba(0, 0, 0, 0.05); }
    .header__menu__item:first-child {
      display: none;
      margin-right: 0; }
  .header__menu__item a {
    display: block;
    padding: 15px 10px 12px;
    text-align: center;
    color: #383838; }
  .header__contacts {
    width: auto; }
  .header__phone {
    font-size: 14px; }
  .header__button {
    display: none; } }

@media (max-width: 767px) {
  .header__wrapper {
    padding: 7px 20px 6px; }
  .header__logo {
    margin-right: 0; }
  .header__toggle {
    width: 18px;
    height: 14px;
    margin-left: auto; }
    .header__toggle::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 18px;
      height: 2px;
      background-color: rgba(0, 0, 0, 0.3);
      box-shadow: 0 6px 0 0 rgba(0, 0, 0, 0.3), 0 12px 0 0 rgba(0, 0, 0, 0.3); }
    .header__toggle--open {
      width: 14px;
      height: 14px; }
      .header__toggle--open::before, .header__toggle--open::after {
        top: 6px;
        left: -2px;
        width: 18px;
        box-shadow: none; }
  .header__menu {
    top: 50px;
    width: 100%; }
  .header__contacts {
    display: none; } }
